/*--------------------------------------------------------------
## Slick Elements
--------------------------------------------------------------*/

.slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;

    li {
        font-size: 0;
        line-height: 0;
        padding: 0;
        margin: 0 5px;

        &.slick-active {
            button {
                transform: scale(1.3);
                background-color: $green;
            }
        }
    }

    button {
        font-size: 0;
        line-height: 0;
        padding: 0;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $black;
        border: 0;
        transition: 0.2s ease-in-out;

        @include hover {
            transform: scale(1.3);
        }

        &:focus {
            outline: none;
        }
    }
}