/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	font-family: $font-main;
	color: $black;
	font-size: 16px;
	line-height: 1.5;
}

pre {
	background: #ededed;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
	font-size: 12px;
	margin-bottom: 30px;
}

.page-title {
	font-size: 45px;
	font-weight: bold;
	line-height: 1.5;

	@include mq($lg) {
		font-size: 34px;
	}

	@include mq($sm) {
		font-size: 30px;
	}
}

.section-title {
	font-size: 35px;
	line-height: 1.25;
	display: block;
	font-weight: bold;

	@include mq($lg) {
		font-size: 30px;
	}

	@include mq($sm) {
		font-size: 24px;
	}
}

.title-small {
	font-size: 24px;
	line-height: 1.25;
	display: block;
	font-weight: 500;

	@include mq($lg) {
		font-size: 20px;
	}
}

/*--------------------------------------------------------------
## Entry Content
--------------------------------------------------------------*/
.entry-content {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-bottom: 15px;
		font-weight: bold;
	}

	h1 {
		font-size: 30px;
	}

	h2 {
		font-size: 24px;
	}

	h3 {
		font-size: 18px;
	}

	h4 {
		font-size: 14px;
	}

	h5 {
		font-size: 12px;
	}

	h6 {
		font-size: 10px;
	}

	p {
		margin-bottom: 25px;
	}

	li {
		margin-bottom: 4px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	ul,
	ol {
		margin-bottom: 27px;

		li {
			margin-bottom: 10px;

			ul {
				padding-top: 10px;
				margin-bottom: 0;
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	ul {
		margin-left: 35px;
	}

	ol {
		margin-left: 43px;

		li {
			list-style: decimal;
		}
	}

	a {
		display: inline-block;
		position: relative;
		color: $red;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			height: 1px;
			width: 0;
			background: $red;
			transition: $dur $ease;
		}

		&:hover {
			&::before {
				width: 100%;
			}
		}
	}

	figure {
		border: 1px solid $white;
		border-radius: 5px;
		overflow: hidden;

		a {
			display: block;
			transition: $dur $ease;

			&::before {
				content: none;
			}

			&:hover {
				opacity: .6;
			}
		}

		.size-full {
			width: 100%;
			display: block;
			opacity: .9;
		}

		.wp-caption-text {
			background: $white;
			color: $grey;
			margin: 0;
			padding: 5px;
			font-size: 12px;
		}
	}

	table {
		color: $grey;
		margin: 25px 0 35px;
		overflow-x: visible;
		table-layout: fixed;
		width: 100%;

		&,
		th,
		td {
			border: 0;
			text-align: left;
			padding: 8px;
		}

		th {
			font-weight: bold;
		}

		tr:nth-child(even) {
			background-color: $grey-light;
		}

		@include mq($lg) {
			display: block;
			overflow-x: scroll;
			max-width: 700px;
		}
	}

	tbody {
		width: 100%;

		@include mq($lg) {
			width: 700px;
			display: block;
		}
	}

	blockquote {
		background: $grey-light;
		border-left: 10px solid $red;
		margin: 30px;
		padding: 20px 30px;
		quotes: '\201C''\201D''\2018''\2019';

		&::before {
			color: $grey-mid;
			content: open-quote;
			font-size: 4em;
			line-height: 0.1em;
			margin-right: 0.25em;
			vertical-align: -0.4em;
		}
	}
}
