/*--------------------------------------------------------------
## banner
--------------------------------------------------------------*/
.banner {
	height: 100vh;
	position: relative;
	max-height: 600px;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black, .75);
		z-index: 1;
	}

	@include mq($lg) {
		max-height: 500px;
	}

	@include mq($sm) {
		max-height: 400px;
	}
}

.banner__caption {
	@include center;
	text-align: center;
	color: $white;
	z-index: 1;
	width: calc(100% - 30px);
	max-width: 600px;
}

.banner__image {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	overflow: hidden;
}

.banner__caption-icon {
	color: $white;
	font-size: 20px;
}

.banner__btn {
	margin-top: 30px;
}
