/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
input[type='submit'],
.btn {
	display: inline-block;
	text-align: center;
	border: 1px solid $black;
	border-radius: 30px;
	background-color: $white;
	color: $black;
	padding: 5px 10px;
	outline: none;
	min-width: 160px;
	transition: $dur $ease;

	@include hover {
		background-color: $black;
		color: $white;
	}

	&:focus {
		outline: none;
		box-shadow: none;
	}
}

.btn--big {
	min-width: 250px;
	padding: 10px 20px;
}

.btn--green {
	border: 1px solid $green;
	background-color: $green;
	color: $white;

	@include hover {
		background-color: $white;
		color: $green;
	}

	&:focus {
		outline: none;
		box-shadow: none;
	}
}
