/*--------------------------------------------------------------
## Popup
--------------------------------------------------------------*/
.popup-holder {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    z-index: 1001;
    transition: 0.2s ease-in-out;

    &.opened {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
}

.popup-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba($white, 0.85);
}

.popup-content {
    color: $white;
    position: fixed;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $red;
    border-radius: 8px;
    width: calc(100% - 40px);
    max-width: 450px;
    z-index: 1;
    padding: 35px;

    @include mq($sm) {
        padding: 40px 20px 35px;
    }
}

.popup-title {
    color: $white;
    font-size: 25px;
    line-height: 1.2;
    text-align: center;
    margin-bottom: 20px;
}

.popup-text {
    font-size: 17px;
    line-height: 1.35;
    color: $white;
    text-align: center;
}

.popup-close {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 20px;
    top: 20px;
    transition: 0.2s ease-in-out;

    &::before,
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 1px;
        height: 100%;
        background-color: $white;
    }

    &::before {
        transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
    }

    @include hover {
        transform: rotate(180deg);
        opacity: 0.7;
    }
}