/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
	background-color: $black;
	padding: 100px 0 15px;

	a {
		color: $white;
	}

	@include mq($md) {
		padding-top: 60px;
	}
}

.site-footer__top {
	margin-bottom: 35px;
}

.site-footer__box {
	@include mq($md) {
		margin-bottom: 35px;
		text-align: center;

		&:last-of-type {
			margin-bottom: 0;
		}
	}
}

.site-footer__logo {
	display: inline-block;

	@include mq($md) {
		margin: 0 auto;
	}
}

.site-footer__box-title {
	color: $white;
	margin-bottom: 20px;
	display: block;
}

.copyright {
	padding: 20px 15px;
	text-align: center;
	line-height: 1.3;
	border-top: 1px solid rgba($white, 0.1);

	p {
		color: $white;
		margin: 0;
	}
}

.site-footer__nav-list {
	margin-bottom: 8px;

	&:last-of-type {
		margin-bottom: 0;
	}
}

.site-footer__nav-link {
	font-size: 17px;
	line-height: 1.2;
	transition: 0.2s ease-in-out;

	@include hover {
		color: $green;
	}
}

.site-footer__social {
	display: flex;
	align-items: center;
	flex-wrap: wrap;

	@include mq($md) {
		justify-content: center;
	}
}

.site-footer__social-item {
	margin: 0 10px;
}

.site-footer__social-link {
	font-size: 25px;
	color: $white;
	transition: 0.2s ease-in-out;

	@include hover {
		color: $green;
	}
}
