/*--------------------------------------------------------------
## story-block
--------------------------------------------------------------*/
.story-block {
    padding: 100px 0;

    @include mq($lg) {
        padding: 40px 0;
    }
}

.story-block--row {
    align-items: center;

    .story-block--alt & {
        @include mq($lg + 1, min) {
            flex-direction: row-reverse;
        }
    }
}

.story-block__content-title {
    margin-bottom: 35px;

    @include mq($lg) {
        margin-bottom: 25px;
    }
}

.story-block__content {
    @include mq($lg) {
        margin-bottom: 30px;
    }
}
